#project {
    height: auto;
    background: ivory;
    padding: 1.5rem 0; 
}

.project-title {
    margin: 1.5rem auto;
    width: auto;
    text-align: center;
}

.project-title h5 {
    font-size: 1.35rem;
    line-height: 2rem;
}
.project-title h2 {
    font-size: 2rem;
}

.project-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2.5rem;
}

.project-items {
    background: #ffffff;
    padding: 1.3rem;
    border-radius: 1rem;
    border: 1px solid transparent;
    transition: var(--trst);
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.66);
}

.project-items:hover {
    border-color: #f3f3f3;
    background: transparent;
}

.project-item-img {
    border-radius: .5rem;
    overflow: hidden;
    /* box-shadow: 0 0 30px rgba(0, 0, 0, 0.26); */
}

.project-item-img img:hover {
    transition: var(--trst);
    transform: scale(1.2);
}

.project-items h3 {
    text-align: center;
    margin: 1rem 0 2rem;
}

.project-item-cta {
    display: flex;
    justify-content: center;
    gap: .65rem;
    margin-bottom: .8rem;   
}

.project-item-cta svg {
    font-size: 1.5rem;
}

.project-item-cta .btn {
    background: #030030;
    letter-spacing: 2px;
}

.project-item-cta .btn:hover {
    background: #ffffff;
    border: 1px solid rgba(0, 0, 0, 0.66);
    color: #000;
}

/* media query */
@media (max-width: 1024px) {
    .project-container {
        grid-template-columns: 1fr 1fr;
        gap: 1.2rem;
    }
}

@media (max-width: 614px) {
    .container.project-container {
        width: 83%;
    }
    #project h2 {
        text-decoration: underline;
    }
    .project-container {
        grid-template-columns: 1fr;
        gap: 1rem;
    }
    .project-items {
        padding: 1.6rem;
    }
}