#about {
    background-color: aliceblue;
    margin: 0 auto;
    height: 100vh;
}

.about-container {
    position: relative;
    width: 80%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2rem;
}

.titlebox {
    position: relative;
    margin: 1rem auto;
    padding: 2rem 0;
    text-align: center;
    letter-spacing: 1px;
}

.titlebox h3 {
    line-height: 2.5rem;
    font-size: 1.2rem;
}

.titlebox h1 {
    font-size: 3rem;
}

.titlebox::before {
    content: "<title>";
    font-size: 82.5%;
    position: absolute;
    margin-top: -30px;
    left: -10px;
    opacity: .75;
    line-height: 18px;
}

.titlebox::after {
    content: "<title>";
    font-size: 82.5%;
    position: absolute;
    left: -30px;
    bottom: -10px;
    margin-left: 20px;
    opacity: .75;
    line-height: 18px;
}

.about-content {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2.2rem;

}

.about-container .about-info {
    margin: 1rem 0;
    width: 50%;
    border: 1px solid transparent;
}

.infomation {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    text-align: center;
}

.infomation .text-title {
    font-size: 2rem;
}

.about-img {
    width: 80%;
}

@media (max-width: 1024px) {}

@media (max-width: 614px) {
    .about-content {
        flex-direction: column;
    }

    .about-content .about-img {
        padding: 0 2rem;
    }

    .infomation .text-title {
        font-size: 1.1rem;
        font-weight: 500;
    }
}