.footer {
    background: salmon;
    padding: 3rem 0;
    text-align: center;
    font-size: .9rem;
    margin-top: 7rem;
}


.footer-logo {
    font-size: 1.85rem;
    padding: .5rem 1rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
    box-shadow: 1px 2px 1px rgba(0, 0, 0, .55);
    background: rgb(236, 149, 140);
    border-radius: 1.8rem;
}

.links {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 50px);
    align-items: center;
    margin: 2rem 1rem;
    font-size: 1.2rem;
}

.links a {
    padding: .8rem;
    background: rgba(0, 0, 0, .35);
    text-shadow: 0 4px 1px rgba(0,0,0,.25);
    border-radius: 12px;
}

.footer-social {
    display: flex;
    justify-content: center;
    gap: 1.2rem;
    margin-top: 1.5rem;
    margin-bottom: 3rem;
}

.footer-social a {
    background: rgba(0, 0, 0, .55);
    color: whitesmoke;
    font-size: 1.2rem;
    padding: .8rem;
    border-radius: 12px;
    display: flex;
    border: 1px solid transparent;
    box-shadow: 0 5px 2px rgba(0,0,0,.33);
}

.footer-social a:hover {
    background: transparent;
    color: #030303;
}

.footer-copy {
    margin-bottom: 3rem;
}

/* @media (max-width: 1024px) {
} */

@media (max-width: 614px) {
    .links {
        flex-direction: column;
        gap: 1.2rem;
    }
    .footer-social {
        margin-bottom: 2rem;
    }
    .footer-copy {
        margin-bottom: 1rem;
    }
}